import './App.css';
import Navbar from "./components/Navbar"
import News from './components/News';
import { BrowserRouter as Main, Routes, Route } from "react-router-dom";
import React, { useState } from 'react';
import LoadingBar from 'react-top-loading-bar'
import Err from './components/Err';
import Card from './components/Card';

function App() {
  


  const [progress, setProgress] = useState(0)
  const key = process.env.REACT_APP_NEWS_API;
  const pageSize = 9;
  return (
    <div>
        <Main>
        <LoadingBar
          height={3}
          color='#f11946'
          progress={progress}
        />
        <Navbar />
        <Routes>
          <Route exact path='/' element={<News key="general" setProgress={setProgress} country={'in'} category={"general"} apiKey={key} pageSize={pageSize} />} />
          <Route exact path='/features' element={<News key="features" setProgress={setProgress} country={'in'} category={"features"} apiKey={key} pageSize={pageSize} />} />
          <Route exact path='/business' element={<News key="business" setProgress={setProgress} country={'in'} category={"business"} apiKey={key} pageSize={pageSize} />} />
          <Route exact path='/entertainment' element={<News key="entertainment" setProgress={setProgress} country={'in'} category={"entertainment"} apiKey={key} pageSize={pageSize} />} />
          <Route exact path='/general' element={<News key="general" setProgress={setProgress} country={'in'} category={"general"} apiKey={key} pageSize={pageSize} />} />
          <Route exact path='/health' element={<News key="health" setProgress={setProgress} country={'in'} category={"health"} apiKey={key} pageSize={pageSize} />} />
          <Route exact path='/science' element={<News key="science" setProgress={setProgress} country={'in'} category={"science"} apiKey={key} pageSize={pageSize} />} />
          <Route exact path='/sports' element={<News key="sports" setProgress={setProgress} country={'in'} category={"sports"} apiKey={key} pageSize={pageSize} />} />
          <Route exact path='/technology' element={<News key="technology" setProgress={setProgress} country={'in'} category={"technology"} apiKey={key} pageSize={pageSize} />} />
          <Route exact path='/*' element={<Err />} />
        </Routes>
      </Main>
    </div>
  );
}

export default App;

