import Card from './Card'
import React, { useEffect, useState } from 'react'
import moment from "moment";
import Spinner from "./Spinner"
import InfiniteScroll from 'react-infinite-scroll-component';
import { FloatButton } from 'antd';
import { BgColorsOutlined } from '@ant-design/icons';



const NewsItem = (props) => {
    const [darkMode, setDarkMode] = useState("light");
    const toggleMode = () => {
        if (darkMode === "light") {
            setDarkMode("dark");
            document.body.style.backgroundColor = "#000000"
        } else {
            setDarkMode("light");
            document.body.style.backgroundColor = "white"
        }
    }
    const capitilizeFunc = (string) => {
        let word = string.toLowerCase();
        return word.charAt(0).toUpperCase() + word.slice(1)
    }

    const [articles, setArticles] = useState([])
    const [loading, setLoading] = useState(true)
    const [page, setPage] = useState(1)
    const [totalResults, setTotalResults] = useState(0)

    const upDateNews = async () => {
        document.title = `${capitilizeFunc(props.category)} - MK News`
        props.setProgress(10)
        const url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apiKey}&page=${page}$pageSize=${props.pageSize}`
        setLoading(true)
        props.setProgress(30)
        const data = await fetch(url);
        props.setProgress(70)
        const parsedData = await data.json()
        setArticles(parsedData.articles)
        setTotalResults(parsedData.totalResults)
        setLoading(false)
        props.setProgress(100);
    }

    useEffect(() => {
        upDateNews()
    }, [])

    const fetchMoreData = async () => {
        const url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apiKey}&page=${page + 1}$pageSize=${props.pageSize}`
        setPage(page + 1)
        setLoading(true)
        const data = await fetch(url);
        const parsedData = await data.json()
        setArticles(articles.concat(parsedData.articles))
        setTotalResults(parsedData.totalResults)
        setLoading(false)
    }
    return (
        <>
            <FloatButton icon={<BgColorsOutlined />} onClick={() => toggleMode()} />
            <h1 className='text-center' style={{ marginTop: 65,color : darkMode === "dark" ? "white" :"black"  }}>News of {capitilizeFunc(props.category)}</h1>
            {loading && <Spinner class={"example"} />}
            <InfiniteScroll
                dataLength={articles.length} //This is important field to render the next data
                next={fetchMoreData}
                hasMore={articles.length !== totalResults.length}
                loader={<Spinner class={"example-1"} />}>
                <div className="container">
                    <div className="row">
                        {articles.map((element) => {
                            return <Card key={element.url} mode={darkMode} imageUrl={element.urlToImage ? element.urlToImage : "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRjCAfVgATBaPFFWX2WWJF6x-gVW4P1mdvfKA&usqp=CAU"} title={element.title ? element.title : ""} url={element.url ? element.url : ""} description={element.description ? element.description.slice(0, 80) + " ...." : ""} date={moment(element.publishedAt).fromNow()} />
                        }
                        )}
                    </div>
                </div>
            </InfiniteScroll>
        </>
    )

}

export default NewsItem;