import React from 'react';
import { Card } from 'antd';
import Button from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css';
const { Meta } = Card;

const App = (props) => (
        <>
        <div className='col mx-2 my-4'  key={props.key}>
            <Card class
                hoverable
                style={{
                    width: 350,
                }}
                cover={<img alt="example" src={props.imageUrl} />}
                >
                <Meta title={props.title} description={props.description} />
                <div className="sm mt-3 mb-2">
                <a href={props.url} target="_blank"><Button variant={`outline-${props.mode === "dark" ? "danger": "secondary"}`}>Details</Button></a>
                </div>
                <Meta title={props.date} />
            </Card>
            
        </div>
        </>
);
export default App;